/* global button styles */
button.modal-button, button.btn-primary {
  background-color: #efc9a5;
  border-color: #efc9a5;
  color: #000000;
  border-radius: 3rem;
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12); }
  button.modal-button:hover, button.modal-button:active, button.btn-primary:hover, button.btn-primary:active {
    background-color: #fffcd7;
    border-color: #efc9a5;
    color: #000000;
    box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12);
    opacity: .85; }

button.btn-info:focus {
  background-color: #efc9a5;
  border-color: #efc9a5;
  color: #000000;
  border-radius: 3rem;
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12); }

button.btn-info:not(:disabled):not(.disabled):active, button.btn-primary:not(:disabled):not(.disabled):active {
  background-color: #fffcd7;
  border-color: #efc9a5;
  color: #000000;
  opacity: .85; }

.btn-info:not(:disabled):not(.disabled):active:focus {
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12); }

/* Content styling */
header {
  margin: 1rem auto;
  text-align: center; }
  header img {
    width: 25%; }
    @media only screen and (max-width: 767px) {
      header img {
        width: 80%;
        margin-top: 1rem; } }
  header button.modal-button {
    margin: 4rem auto; }

#infoModal {
  background: rgba(0, 0, 0, 0.5); }
  #infoModal .modal-dialog {
    background: #EEEEEE;
    max-width: 1400px; }
    #infoModal .modal-dialog .close {
      background-color: #fffcd7;
      box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12);
      border-radius: 3rem;
      width: 50px;
      margin: 0;
      padding: 6px 0; }
    #infoModal .modal-dialog .modal-header {
      background: #d5716c; }
      #infoModal .modal-dialog .modal-header .close {
        font-size: 2.5rem; }
    #infoModal .modal-dialog .modal-main {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap; }
      @media only screen and (max-width: 767px) {
        #infoModal .modal-dialog .modal-main {
          flex-direction: column; } }
    #infoModal .modal-dialog .modal-footer {
      background: #d5716c; }
      #infoModal .modal-dialog .modal-footer .close {
        font-size: 1rem; }

#couponCode {
  display: inline-block;
  width: 49%; }
  @media only screen and (max-width: 767px) {
    #couponCode {
      width: 100%;
      padding: .5rem; } }
  #couponCode img {
    width: 25%; }
  #couponCode h3 {
    display: inline-block;
    margin-left: 1rem; }
    @media only screen and (max-width: 767px) {
      #couponCode h3 {
        width: 69%; } }
  #couponCode p {
    width: 80%;
    margin: 0 auto; }
  #couponCode .coupon-box {
    margin: 1rem; }
    #couponCode .coupon-box #couponCode {
      display: inline-block;
      background-color: #EEEEEE;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 12px;
      padding: .59rem 0;
      border: 1px solid rgba(0, 0, 0, 0.5);
      width: 77%;
      float: left;
      font-size: 15.63px;
      font-size: 1.563rem;
      z-index: 1; }
      @media only screen and (max-width: 767px) {
        #couponCode .coupon-box #couponCode {
          margin: 0 auto;
          width: 100%; } }
    #couponCode .coupon-box .btn {
      border-radius: 3rem;
      float: right;
      margin-top: -1px;
      margin-left: -2rem;
      z-index: 10; }
      @media only screen and (max-width: 767px) {
        #couponCode .coupon-box .btn {
          margin: 1rem auto; } }

#emailSub {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 49%; }
  @media only screen and (min-width: 1025px) {
    #emailSub {
      transition: visibility 1s .15s ease-in; } }
  @media only screen and (max-width: 767px) {
    #emailSub {
      width: 100%;
      padding: .5rem; }
      #emailSub h3 {
        width: 90%;
        margin: 0 auto; } }
  #emailSub .material-icons {
    color: #efc9a5;
    width: 50%;
    margin: 0 auto; }
  #emailSub ul {
    list-style: none;
    text-align: left;
    margin: 0 auto 2rem auto; }

.active {
  background-color: #fffcd7;
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12); }
  .active .material-icons {
    color: #fffcd7; }
  .active button.btn-primary {
    background-color: #d5716c;
    min-height: 3.125rem; }
    .active button.btn-primary:hover, .active button.btn-primary:not(:disabled):not(.disabled):active {
      background-color: #ffa19a;
      opacity: 1; }
